import { Image, StyleSheet, Text, View } from "react-native";
import React from "react";
import { Product } from "../interfaces/product-response.interface";

interface Props {
  producto: Product;
}

const ListItem = ({ producto }: Props) => {
  const { description, tags, title, gender, images, price } = producto;
  let portada: string | undefined = images[0] ?? undefined;
  portada = portada && "http://192.168.1.151:3000/api/files/product/" + portada;

  return (
    <View style={styles.container}>
      {portada && (
        <Image
          style={styles.image}
          source={{
            uri: portada,
          }}></Image>
      )}
      <View>
        <Text style={styles.title}>{title}</Text>
        <Text style={styles.subtitle}>Precio: ${price}</Text>
        <Text style={styles.description}>{description}</Text>
      </View>
    </View>
  );
};

export default ListItem;

const styles = StyleSheet.create({
  container: {
    justifyContent: "flex-start",
    flex: 1,
    flexDirection: "row",
    marginVertical: 10,
    marginHorizontal: 20,
  },
  image: {
    height: 150,
    width: 100,
    marginRight: 10,
  },
  title: {
    fontSize: 20,
    fontWeight: "bold",
  },
  subtitle: {
    fontSize: 18,
    fontWeight: "bold",
  },
  description: {
    fontSize: 16,
    fontWeight: "normal",
    textAlign: "left",
    width: 600,
  },
});
