import { useEffect, useState } from "react";
import { Product, ProductData } from "../interfaces/product-response.interface";
import { ProductApi } from "../api/products.api";

export const useProducts = () => {
  const productApi = new ProductApi();
  const [loading, setLoading] = useState<boolean>(true);
  const [data, setdata] = useState<Product[]>([]);

  useEffect(() => {
    cargarData();
  }, []);

  const cargarData = async () => {
    try {
      const respuesta = await productApi.get("/api/products", "?limit=40");
      let datosResponse: ProductData = await respuesta.json();
      for (let index = 0; index < datosResponse.data.length; index++) {
        const element = datosResponse.data[index];
        data.push(element);
      }
      setLoading(false);
    } catch (error) {
      setLoading(false);
    }
  };

  return { loading, data };
};
